指引用filter元素的对象的描边区域。 另外，在使用背景时，用于包裹图像的元素还可以在style属性中规定一个\"enable-background\"参数，用来指明是否允许使用图像背景。 值可以取俩个：\"new| accumulate\"。new表示包含元素的子元素可以自由的访问背景(及允许使用 BackgroundImage和BackgroundAlpha)。accumulate表示不允许，同时也是该属性的缺省值。 ")]),_c('h3',[_vm._v(" 光源和光照模式：svg中，光源元素并不独立作用于被照射的物体，而是作为光照模式元素的子元素而存在，光照模式指明了所含有的光源子元素照射时的反射模式，如漫反射、高光等。 svg中光源有三种：平行光、点光源和聚光灯。 feDistantLight:平行光，属性有俩个 azimuth：光线的方位角(光线所在的直线在xy平面上的投影与x轴正方向的夹角，单位deg) elevation：光线的仰角(光线本身与其在xy平面上的投影的夹角) fePointLight:点光源(光源是一个点，光线朝四周均匀发散)，属性有三个： x=\"num\":光源位置的x坐标； y=\"num\":光源位置的y坐标； z=\"num\":光源位置的z坐标； feSpotLight:聚光灯(点光源发射的光集中在一个圆锥范围内，光源刚好是圆锥的顶点)，属性如下： x=\"num\":光源位置的x坐标； y=\"num\":光源位置的y坐标； z=\"num\":光源位置的z坐标； pointsAtX=\"num\":圆锥底部的圆心的x坐标; pointsAtY=\"num\":圆锥底部的圆心的y坐标; pointsAtZ=\"num\":圆锥底部的圆心的z坐标; specularExponent=\"num\":知名光线的聚焦程度； limitingConeAngle=\"num\"：指明圆锥的发散程度，值为圆锥轴线与母线的夹角(圆锥锥角的一半)，如果不说明该值，会无限发散，变成了点光源 光照模式：有镜面反射和漫反射俩种， feDiffuseLighting：漫反射。该元素可以包含多个定义光源的元素，属性如下： surfaceScale=\"num\":透明度到Z坐标(高度)转换的比例因子。透明度为1时转换后的Z坐标极为属性的值。可根据需求设置。 diffuseConstant=\"num\"：指明漫反射系数，可以是任何非0的值，通常为1 feDiffuseLighting元素一般施加到透明度分布有过渡情况的图像对象，这样透明度转换后z坐标分布有过渡，而不是黑白分明，光照的效果也会更明显。 feSpecularLighting:镜面反射。有强烈的立体感。属性如下： surfaceScale=\"num\":透明度到Z坐标(高度)转换的比例因子。透明度为1时转换后的Z坐标极为属性的值。可根据需求设置。 specularConstant=\"num\"：指明镜面反射系数，可以是任何非0的值。 specularExponent=\"num\":镜面反射的范围的广泛程度，值在0-128之间，值越大越接近镜面反射，如果设置为0，几乎等同于漫反射 此外，这俩种元素都可以设置光线颜色，通过style=\"lighting-color:red\"; ")]),_c('svg',{attrs:{"width":"11.3cm","height":"4.2cm","viewBox":"0 0 1130 420"}},[_c('desc',[_vm._v("光照效果实例")]),_c('defs',[_c('filter',{attrs:{"id":"lightingFilter"}},[_c('feGaussianBlur',{attrs:{"in":"sourceAlpha","stdDeviation":"10","result":"blur"}}),_c('feSpecularLighting',{attrs:{"in":"sourceAlpha","lightColor":"red","result":"specularOut"}},[_c('fePointLight',{attrs:{"x":"-500","y":"-1000","z":"2000"}})],1),_c('feMerge',[_c('feMergeNode',{attrs:{"in":"litPaint"}})],1)],1)]),_c('rect',{staticStyle:{"fill":"none","stroke":"black"},attrs:{"x":"5","y":"5","width":"1120","height":"4